@import "./assets/scss/Normalize.scss";
@import "./assets/scss/Fonts.scss";



html{
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}
*,
*::before,
*::after{
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}
body{
    margin: 0;
    padding: 0;
    font-family: 'Mulish', 'Arial', 'Serif';
    background-color:  rgb(23, 24, 22);
    width: 100%;
    height: 100%;
}
#root{
    width: 100%;
    height: 100%;
}